import {useState} from 'react';
import {connect} from 'react-redux';

import RecipeCard from '../../components/RecipeCard';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faBook, faPlus, faTimes} from '@fortawesome/free-solid-svg-icons';
import Recipe from '../../components/Recipe';
import RecipeNew from '../../components/RecipeNew';
import RecipeDb from '../../components/RecipeDb';
import recipeSvg from '../../imgs/recipe.svg';
import {setFeature} from '../../redux';

function RecipesPage(props) {
    const [pageOpen, setPageOpen] = useState('recipes');
    const [currentRecipe, setCurrentRecipe] = useState();

    const generateRecipeCards = () => {
        const tempCards = [];
        tempCards.push(<div className="mt-4"></div>);
        tempCards.push(props.userData.recipes.map((recipe, index) => <RecipeCard title={recipe.title} desc={recipe.desc} recipe={recipe.recipe} setCurrentRecipe={setCurrentRecipe} index={index+1}/>));
        return tempCards;
    }
    return (
        <div>
            {pageOpen == 'recipes' ?
                <div>
                    <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                        <img className="card-img-top" src={recipeSvg} style={{width: '9rem'}}/>
                        {currentRecipe ? '' : <button className="btn"><FontAwesomeIcon onClick={() => setPageOpen('recipesDb')} className="recipeFeatureBack" style={{width: '7rem'}} icon={faBook} size="8x"/></button>}
                        {currentRecipe ? <div className="h1 container" style={{transform: 'translateY(20%)'}}>{props.userData.recipes[currentRecipe-1] ? props.userData.recipes[currentRecipe-1].title : ''}</div> : <button className="btn"><FontAwesomeIcon onClick={() => setPageOpen('recipesNew')} className="recipeFeatureBack" style={{width: '7rem'}} icon={faPlus} size="8x"/></button>}
                        <button className="btn" onClick={() => currentRecipe ? setCurrentRecipe('') : props.setFeature('')}><FontAwesomeIcon className="recipeFeatureBack" size="8x" icon={faTimes}/></button>
                    </div>
                    {!currentRecipe ?
                    generateRecipeCards()
                    :
                    <Recipe setCurrentRecipe={setCurrentRecipe} recipe={props.userData.recipes[currentRecipe-1]} overrideLang={false}/>
                    }
                </div>
                :
                pageOpen == 'recipesNew' ?
                    <RecipeNew setPageOpen={setPageOpen}/>
                :
                pageOpen == 'recipesDb' ?
                    <RecipeDb setPageOpen={setPageOpen} currentRecipe={currentRecipe} setCurrentRecipe={setCurrentRecipe}/>
                :
                ''}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        userData: state.connection.userData,
        feature: state.general.feature
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setFeature: val => dispatch(setFeature(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipesPage);