import { faPlusCircle, faTimes, faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useState } from "react";
import recipeSvg from '../imgs/recipe.svg';
import axios from "axios";
import {connect} from 'react-redux';
import {setData} from '../redux/';

import langJson from '../json/language.json';

function RecipeNew(props) {
    const [recipeTitle, setRecipeTitle] = useState(false);
    const [recipeDesc, setRecipeDesc] = useState(false);
    const [recipeIngredients, setRecipeIngredients] = useState(false);
    const [recipeInstructions, setRecipeInstructions] = useState(false);

    const [milkBool, setMilkBool] = useState(false);
    const [meatBool, setMeatBool] = useState(false);
    const [veganBool, setVeganBool] = useState(false);
    const [ovenBool, setOvenBool] = useState(false);
    const [fryingBool, setFryingBool] = useState(false);

    const setBool = type => {
        switch(type) {
            case 'vegan':
                setMilkBool(false);
                setMeatBool(false);
                setVeganBool(true);
                break;
            case 'milk':
                setVeganBool(false);
                setMilkBool(true);
                break;
            case 'meat':
                setVeganBool(false);
                setMeatBool(true);
                break;
        }
    }

    function resizeTextarea(el){
        const windowEl = window.getComputedStyle(el);
        setTimeout(function(){
          el.style.cssText = 'height:auto; padding:0;';
          el.style.cssText = 'resize:none; height:' + (17 + el.scrollHeight + parseInt(windowEl.getPropertyValue('border-top-width')) + parseInt(windowEl.getPropertyValue('padding-top')) + parseInt(windowEl.getPropertyValue('padding-bottom')) + parseInt(windowEl.getPropertyValue('border-bottom-width'))) + 'px';
        },0);
    }

    const saveRecipe = authToken => {
        axios.post('https://recipesheroku.herokuapp.com/recipeadd', {authToken, recipe: {
            title: recipeTitle,
            desc: recipeDesc,
            recipe: {
                ingredients: recipeIngredients,
                instructions: recipeInstructions,
                milk: milkBool,
                meat: meatBool,
                vegan: veganBool,
                oven: ovenBool,
                frying: fryingBool
            }
        }}).then(response => response.data)
        .then(json => {
            if(json.bool) {
                props.setData(json.data);
                props.setPageOpen('recipes');
            }
        })
        .catch(() => {});
    }

    useEffect(() => {
        resizeTextarea(document.getElementById('ingTextarea'));
        resizeTextarea(document.getElementById('insTextarea'));
    }, []);

    return (
        <div style={{backgroundColor: '#17a2b8'}}>
            <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                <img className="card-img-top" src={recipeSvg} style={{width: '9rem'}}/>
                <input onChange={e => setRecipeTitle(e.target.value)} placeholder={langJson[props.lang][20]} className="text-center w-100 display-3"/>
                <button className="btn" onClick={() => props.setPageOpen('recipes')}><FontAwesomeIcon className="recipeFeatureBack" size="8x" icon={faTimes}/></button>
            </div>
            <div style={{left: '2rem'}}>
                <div dir={langJson[props.lang][0]} className={`btn badge ${milkBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !milkBool ? setBool('milk') : setMilkBool(false)}>{langJson[props.lang][15]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${meatBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !meatBool ? setBool('meat') : setMeatBool(false)}>{langJson[props.lang][16]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${veganBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !veganBool ? setBool('vegan') : setVeganBool(false)}>{langJson[props.lang][17]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${ovenBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => setOvenBool(!ovenBool)}>{langJson[props.lang][18]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${fryingBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={e => setFryingBool(!fryingBool)}>{langJson[props.lang][19]}?</div>
            </div>
            <form onSubmit={e => [e.preventDefault(), saveRecipe(localStorage.getItem('authToken'))]}>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][12]}:</div>
                <input dir={langJson[props.lang][0]} onChange={e => setRecipeDesc(e.target.value)}/>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][13]}:</div>
                <textarea dir={langJson[props.lang][0]} id="ingTextarea" onChange={e => [resizeTextarea(e.target), setRecipeIngredients(e.target.value)]}></textarea>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][14]}:</div>
                <textarea dir={langJson[props.lang][0]} id="insTextarea" onChange={e => [resizeTextarea(e.target), setRecipeInstructions(e.target.value)]}></textarea>
                <br/>
                <button type="submit" className="btn btn-primary my-2 px-4">{langJson[props.lang][21]}</button>
            </form>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setData: val => dispatch(setData(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipeNew);