import {useState} from 'react';
import recipeSvg from '../imgs/recipe.svg';
import {connect} from 'react-redux';
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Recipe from './Recipe';
import RecipeCard from './RecipeCard';
import langJson from '../json/language.json';
import recipesJson from '../json/recipes.json';

function RecipeDb(props) {

    const generateRecipeCards = () => {
        const tempCards = [];
        tempCards.push(<div className="mt-4"></div>);
        tempCards.push(recipesJson.map((recipe, index) => <RecipeCard title={recipe.title} desc={recipe.desc} recipe={recipe.recipe} setCurrentRecipe={props.setCurrentRecipe} index={index+1}/>));
        return tempCards;
    }
    return (
        <div>
            <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                <img className="card-img-top" src={recipeSvg} style={{width: '9rem'}}/>
                <div className="h1 container h-100" style={{transform: 'translateY(20%)'}}>{recipesJson[props.currentRecipe-1] ? recipesJson[props.currentRecipe-1].title : ''}</div>
                <button className="btn" onClick={() => props.currentRecipe ? props.setCurrentRecipe('') : props.setPageOpen('recipes')}><FontAwesomeIcon className="recipeFeatureBack" size="8x" icon={faTimes}/></button>
            </div>
            <div>
                {props.currentRecipe ?
                <Recipe setCurrentRecipe={props.setCurrentRecipe} recipe={recipesJson[props.currentRecipe-1]} overrideLang="en"/>
                :
                generateRecipeCards()
                }
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipeDb);