import meatSvg from '../imgs/meat.svg';
import milkSvg from '../imgs/milk.svg';
import broccoliSvg from '../imgs/broccoli.svg';
import ovenSvg from '../imgs/oven.svg';
import fryingPanSvg from '../imgs/frying-pan.svg';
import axios from 'axios';
import {connect} from 'react-redux';
import {setData, setLanguage} from '../redux/';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faEdit, faSave } from '@fortawesome/free-solid-svg-icons';
import { useEffect, useState } from 'react';
import whiskSvg from '../imgs/whisk.svg';

import langJson from '../json/language.json';

function Recipe(props) {

    const [editMode, setEditMode] = useState(false);
    const [editTitle, setEditTitle] = useState(props.recipe.title);
    const [editDesc, setEditDesc] = useState(props.recipe.desc);
    const [editIngredients, setEditIngredients] = useState(props.recipe.recipe.ingredients);
    const [editInstructions, setEditInstructions] = useState(props.recipe.recipe.instructions);
    const [editMilk, setEditMilk] = useState(props.recipe.recipe.milk);
    const [editMeat, setEditMeat] = useState(props.recipe.recipe.meat);
    const [editVegan, setEditVegan] = useState(props.recipe.recipe.vegan);
    const [editOven, setEditOven] = useState(props.recipe.recipe.oven);
    const [editFrying, setEditFrying] = useState(props.recipe.recipe.frying);
    if(props.overrideLang) {
        props.setLanguage(props.overrideLang);
    }

    const makeBadges = () => {
        const tempIcons = [];
        if(props.recipe.recipe.meat) {
            tempIcons.push(<img  style={{width: '10rem'}} className="mx-2" src={meatSvg}/>);
            tempIcons.push(<br/>);
            tempIcons.push(<div style={{height: '10rem', transform: 'translateX(5rem)', border: '4px dashed black', borderStyle: 'none none none dashed'}} className="my-3"></div>)
        }
        if(props.recipe.recipe.milk) {
            tempIcons.push(<img  style={{width: '10rem'}} className="mx-2" src={milkSvg}/>);
            tempIcons.push(<br/>);
            tempIcons.push(<div style={{height: '10rem', transform: 'translateX(5rem)', border: '4px dashed black', borderStyle: 'none none none dashed'}} className="my-3"></div>)
        }
        if(props.recipe.recipe.vegan) {
            tempIcons.push(<img  style={{width: '10rem'}} className="mx-2" src={broccoliSvg}/>);
            tempIcons.push(<br/>);
            tempIcons.push(<div style={{height: '10rem', transform: 'translateX(5rem)', border: '4px dashed black', borderStyle: 'none none none dashed'}} className="my-3"></div>)
        }
        if(props.recipe.recipe.oven) {
            tempIcons.push(<img style={{width: '10rem'}} className="mx-2" src={ovenSvg}/>);
            tempIcons.push(<div style={{height: '10rem', transform: 'translateX(5rem)', border: '4px dashed black', borderStyle: 'none none none dashed'}} className="my-3"></div>)
            tempIcons.push(<br/>);
        }
        if(props.recipe.recipe.frying) {
            tempIcons.push(<img style={{width: '10rem'}} className="mx-2" src={fryingPanSvg}/>);
            tempIcons.push(<br/>);
            tempIcons.push(<div style={{height: '10rem', transform: 'translateX(5rem)', border: '4px dashed black', borderStyle: 'none none none dashed'}} className="my-3"></div>)
        }
        if(tempIcons[0]) {
            tempIcons.pop();
            tempIcons.pop();
        } else {
            tempIcons.push(<div style={{width: '11rem', transform: 'translateX(5rem)', border: '4px dashed black', borderStyle: 'none none none dashed'}} className="my-3"></div>)
        }
        return tempIcons;
    }

    const deleteRecipe = authToken => {
        axios.post('https://recipesheroku.herokuapp.com/recipedel', {authToken, recipeId: props.recipe.id})
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                props.setCurrentRecipe('');
                props.setData(json.data);
            } else {
                console.log('nopers');
            }
        })
        .catch(() => {});
    }

    const saveRecipe = authToken => {
        const tempData = {
            title: editTitle,
            desc: editDesc,
            recipe: {
                ingredients: editIngredients,
                instructions: editInstructions,
                milk: editMilk,
                meat: editMeat,
                vegan: editVegan,
                oven: editOven,
                frying: editFrying
            },
            id: props.recipe.id
        }
        axios.post('https://recipesheroku.herokuapp.com/recipeedit', {authToken, recipeId: props.recipe.id, data: tempData})
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                props.setData(json.data);
                props.setCurrentRecipe('');
            }
        })
    }

    const setBool = type => {
        switch(type) {
            case 'vegan':
                setEditMilk(false);
                setEditMeat(false);
                setEditVegan(true);
                break;
            case 'milk':
                setEditVegan(false);
                setEditMilk(true);
                break;
            case 'meat':
                setEditVegan(false);
                setEditMeat(true);
                break;
        }
    }
    
    function resizeTextarea(el){
        const windowEl = window.getComputedStyle(el);
        setTimeout(function(){
          el.style.cssText = 'height:auto; padding:0;';
          el.style.cssText = 'resize:none; height:' + (17 + el.scrollHeight + parseInt(windowEl.getPropertyValue('border-top-width')) + parseInt(windowEl.getPropertyValue('padding-top')) + parseInt(windowEl.getPropertyValue('padding-bottom')) + parseInt(windowEl.getPropertyValue('border-bottom-width'))) + 'px';
        },0);
    }


    useEffect(() => {
        if(editMode) {
            resizeTextarea(document.getElementById('ingTextarea'));
            resizeTextarea(document.getElementById('insTextarea'));
        }
    }, [editMode]);

    const checkForChanges = () => {
        if(editTitle == props.recipe.title && editDesc == props.recipe.desc && editIngredients == props.recipe.recipe.ingredients && editInstructions == props.recipe.recipe.instructions && editMilk == props.recipe.recipe.milk && editMeat == props.recipe.recipe.meat && editVegan == props.recipe.recipe.vegan && editOven == props.recipe.recipe.oven && editFrying == props.recipe.recipe.frying) {
            setEditMode(false);
        } else if(window.confirm(`${langJson[props.lang][22]}?`)) {
            setEditTitle(props.recipe.title);
            setEditDesc(props.recipe.desc);
            setEditIngredients(props.recipe.recipe.ingredients);
            setEditInstructions(props.recipe.recipe.instructions);
            setEditMilk(props.recipe.recipe.milk);
            setEditMeat(props.recipe.recipe.meat);
            setEditVegan(props.recipe.recipe.vegan);
            setEditOven(props.recipe.recipe.oven);
            setEditFrying(props.recipe.recipe.frying);
            setEditMode(false);
        }
    }

    const generateIngredients = () => {
        return props.recipe.recipe.ingredients.split(`\n`).map(val => <li><img dir={langJson[props.lang][0]} src={whiskSvg} style={{transform: `translate(${props.lang == 'en' ? '-' : ''}150%, 25%)`, width: '16px'}} className="ml-1 position-absolute"/>{val}</li>);
    }

    return (
        <div style={{backgroundColor: '#17a2b8'}}>
            <div className="position-absolute" style={{backgroundColor: '#17a2b8', border: '2px solid black', borderStyle: 'none solid none none', transform: 'translateY(-2px)', height: '10rem', width: '178px'}}></div>
            <div className="position-absolute h-100" style={{marginTop: '159px' ,backgroundColor: '#17a2b8', border: '2px solid black', borderStyle: 'none solid none none', transform: 'translateY(-2px)'}}>
                {makeBadges()}
            </div>
            <div className="position-absolute" style={{right: 0}}>
                {editMode ? 
                [
                <button onClick={checkForChanges} className="btn m-4"><FontAwesomeIcon icon={faEdit} className="recipeFeatureBack" size="8x"/></button>,
                <br/>,
                <button onClick={() => window.confirm(`${langJson[props.lang][23]}?`) ? saveRecipe(localStorage.getItem('authToken')) : ''} className="btn m-4"><FontAwesomeIcon icon={faSave} className="recipeFeatureBack" size="5x"/></button>,
                <br/>,
                <button onClick={() => window.confirm(`${langJson[props.lang][24]}?`) ? deleteRecipe(localStorage.getItem('authToken')) : ''} className="btn m-4"><FontAwesomeIcon icon={faTrash} className="recipeFeatureBack" size="5x"/></button>
                ]
                :
                <button className="btn" onClick={() => setEditMode(true)}><FontAwesomeIcon className="recipeFeatureBack" style={{width: '7rem'}} icon={faEdit} size="8x"/></button>
                }
            </div>
            {editMode ?
            <div>
                <div style={{left: '2rem'}}>
                    <div dir={langJson[props.lang][0]} className={`btn badge ${editMilk ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !editMilk ? setBool('milk') : setEditMilk(false)}>{langJson[props.lang][15]}?</div>
                    <div dir={langJson[props.lang][0]} className={`btn badge ${editMeat ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !editMeat ? setBool('meat') : setEditMeat(false)}>{langJson[props.lang][16]}?</div>
                    <div dir={langJson[props.lang][0]} className={`btn badge ${editVegan ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !editVegan ? setBool('vegan') : setEditVegan(false)}>{langJson[props.lang][17]}?</div>
                    <div dir={langJson[props.lang][0]} className={`btn badge ${editOven ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => setEditOven(!editOven)}>{langJson[props.lang][18]}?</div>
                    <div dir={langJson[props.lang][0]} className={`btn badge ${editFrying ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={e => setEditFrying(!editFrying)}>{langJson[props.lang][19]}?</div>
                </div>
                <div dir={langJson[props.lang][0]} className="h2">{langJson[props.lang][20]}:</div>
                <input dir={langJson[props.lang][0]} style={{whiteSpace: 'pre-wrap'}} onChange={e => setEditTitle(e.target.value)} defaultValue={props.recipe.title}/>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][12]}:</div>
                <input dir={langJson[props.lang][0]} style={{whiteSpace: 'pre-wrap'}} onChange={e => setEditDesc(e.target.value)} defaultValue={props.recipe.desc}/>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][13]}:</div>
                <textarea dir={langJson[props.lang][0]} id="ingTextarea" style={{whiteSpace: 'pre-wrap'}} onChange={e => [resizeTextarea(e.target), setEditIngredients(e.target.value)]}>{props.recipe.recipe.ingredients}</textarea>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][14]}:</div>
                <textarea dir={langJson[props.lang][0]} id="insTextarea" style={{whiteSpace: 'pre-wrap'}} onChange={e => [resizeTextarea(e.target), setEditInstructions(e.target.value)]}>{props.recipe.recipe.instructions}</textarea>
            </div>
            :
            <div>
                {props.recipe.desc ? 
                <div>
                    <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][12]}:</div>
                    <div style={{whiteSpace: 'pre-wrap'}}>{props.recipe.desc}</div>
                </div>
                :
                ''}
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][13]}:</div>
                <ul dir={langJson[props.lang][0]} className="list-unstyled">
                    {generateIngredients()}
                </ul>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][14]}:</div>
                <div style={{whiteSpace: 'pre-wrap', width: '75%'}} className="container">{props.recipe.recipe.instructions}</div>
            </div>}
            <div className="position-absolute h-100 w-100" style={{backgroundColor: '#17a2b8', marginBottom: '159px', transform: 'translateY(-2.5rem)', zIndex: -1}}></div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setData: val => dispatch(setData(val)),
        setLanguage: val => dispatch(setLanguage(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Recipe);