import {SET_LANGUAGE, SET_FEATURE} from './types';

const initialState = {
    lang: 'en'
}

const reducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_LANGUAGE: return {
            ...state,
            lang: action.payload
        }
        case SET_FEATURE: return {
            ...state,
            feature: action.payload
        }
        default: return state;
    }
}

export default reducer;